.App {
  text-align: center;
}
.wrapper {
  max-width: 400px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  min-height: 100vh;
}
.container {
  min-height: 100vh;
  & > :nth-child(2) {
    min-height: calc(100vh - 52px - 60px);
  }
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
